import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
import map from '../../../data/js/map.js'
export default new Vuex.Store({
  // statedata.$store.state.
  state:{
    city:"",
    num:1,
    msg:'',
    list:[
      {
        shop:[
          {
            images:"./data/images/money01.jpg",
            title:" 300g( )",
            price:"21.80",
            amount:"1",
            weight:"0.3",
            intergral:"21.00",
            id:0
          },
          {
            images:"./data/images/money02.jpg",
            title:" 20g*16",
            price:"39.90",
            amount:"1",
            weight:"0.432",
            intergral:"39.00",
            id:1
          }
        ]
      },
      {
        shop:[
          {
            images:"./data/images/money03.jpg",
            title:"8-105 250-300g",
            price:"71.80",
            amount:"1",
            weight:"2.5",
            intergral:"71.00",
            id:0
          }
        ]
      }
    ],
  },
  actions:{
    changeCity(ctx,city){
      console.log(city)
      ctx.commit('changeCity',city)
    },
  },
  // mutations this.$store.commit('',)
  mutations:{
    add(state,id){
      state.num++;

    },
    sub(state,id){
      state.num--;
      if(state.num<0){
        state.num=0
      }
    },
    prev(){
    this.$router.go(-1)
    alert(11111)
    },

    city(state,n){
    map.init().then(BMap =>{
    const locationCity = new BMap.Geolocation();
    var that = this;
    locationCity.getCurrentPosition(
    	          function getinfo(options){
    	            let city = options.address.city;    //
    	             state.city = city;
    	            console.log(options)
                  console.log("city"+ state.city)
    	          },
    	          function (e){
    	            that.city = '';
    	            console.log('')
    	          },
    	          {provider: 'baidu'}
    	        )
    	      })
    },
    changeCity(state,city){
      state.city=city
    }
  },
})



